<template>
  <Common />
  <main id="main" class="main">
    <div class="pagetitle"><h1>{{pageTitle}}</h1></div>
    <div class="box"><span>歡迎</span></div>
  </main>
</template>

<script>
import Common from '@/components/Admin/Common';
import { registrationList } from '@/apis/campaignAdmin';

export default {
  name: 'Admin',
  metaInfo() {
    return {
      meta: [{
        name: 'keyWords',
        content: 'Zoom'
      }]
    }
  },
  components: {
    Common
  },
  data() {
    return {
      pageTitle:this.$route.meta.title
    }
  },
  methods: {
    dialog(dataText,dataIcon) {
      let vm = this;
      return vm.$swal({
        title: "",
        text: dataText,
        icon: dataIcon,
        showCancelButton: false,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#dd3333',
        confirmButtonText: '確定',
        cancelButtonText: '取消'
      });
    },
    getList(){
      let vm = this;
      registrationList('NONE',0,10).then(res => {
        console.log(res)
      }).catch(err => {
        if("ERR_NETWORK" == err.code || 403 == err.response.status){
          vm.dialog('您沒有權限唷，請找開發人員。','error').then((result) => {
            if (result.isConfirmed) {
              location.href = 'https://kol2022.pixnet.net/#/';
            }
          });
        }
      });
    }
  },
  setup() {},
  mounted() {
    let vm = this;
    vm.getList();
  },
  updated() {}
}
</script>
